<template>
    <div id="top">
        <el-dropdown class="setting" trigger="click" @command="handleCommand">
            <span class="el-dropdown-link"><i class="el-icon-setting ch"></i></span>
            <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="logout"></el-dropdown-item>
            </el-dropdown-menu>
        </el-dropdown>
        <div class="setting"> {{userName}}</div>
    </div>
</template>
<script>
export default {
    created() {
            let user = JSON.parse(sessionStorage.getItem('user'))
            this.userName = user.username
        },
        data() {
            return {
                userName: ''
            }
        },
        methods: {
            handleCommand(command) {
               if(command=='logout'){
               		this.logout()
               }
            },
            logout() {
                this.$confirm(', ?', 'Confirm', {
                    confirmButtonText: 'OK',
                    cancelButtonText: 'Cancel',
                    type: 'warning'
                }).then(() => {
                    sessionStorage.setItem('user', null)
                    this.$router.push({
                        name: 'BLogin'
                    })
                }).catch(() => {
                    console.log('error')
                });
            }
        }
}
</script>
<style>
.el-icon-setting {
    color: white;
    font-size: 20px;
}

.setting {
    margin-top: 14px;
    margin-right: 14px;
    float: right;
}
.ch{
	cursor:pointer
}
</style>



