<template>
    <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm card-box loginform">
        <h3 class="title"></h3>
        <el-form-item prop="account">
            <el-input type="text" v-model="ruleForm2.account" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="checkPass">
            <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-checkbox v-model="checked" checked style="margin:0px 0px 35px 0px;"></el-checkbox>
        <el-form-item style="width:100%;">
            <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining"></el-button>
            <!--<el-button @click.native.prevent="handleReset2"></el-button>-->
        </el-form-item>
    </el-form>
</template>
<script>
import NProgress from 'nprogress'
export default {
    data() {
            return {
                logining: false,
                ruleForm2: {
                    account: 'admin',
                    checkPass: '123456'
                },
                loginUrl: '/api/signin',
                rules2: {
                    account: [{
                            required: true,
                            message: '',
                            trigger: 'blur'
                        },
                        //{ validator: validaePass }
                    ],
                    checkPass: [{
                            required: true,
                            message: '',
                            trigger: 'blur'
                        },
                        //{ validator: validaePass2 }
                    ]
                },
                checked: true
            };
        },
        methods: {
            handleReset2() {
                this.$refs.ruleForm2.resetFields();
            },
            handleSubmit2(ev) {
                var _this = this;
                this.$refs.ruleForm2.validate((valid) => {
                    if (valid) {
                        //_this.$router.replace('/table');
                        this.logining = true;
                        NProgress.start();
                        let loginParams = {
                            username: this.ruleForm2.account,
                            password: this.ruleForm2.checkPass
                        }
                        let _this = this
                        _this.$resource(this.loginUrl).save(loginParams)
                            .then((response) => {
                                _this.logining = false;
                                NProgress.done();
                                if (response.body.status) {
                                    this.$notify({
                                        message: '!',
                                        type: 'error'
                                    })
                                } else {
                                	sessionStorage.setItem('user', JSON.stringify(response.body));
                                    this.$router.push({
                                        name: 'BIndex'
                                    })
                                }

                            })
                            .catch(function(response) {
                                console.log(response)
                            })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        }
}
</script>
<style scoped>
.card-box {
    padding: 20px;
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin-bottom: 20px;
    background-color: #F9FAFC;
    margin: 180px auto;
    width: 400px;
    border: 2px solid #8492A6;
}

.title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
}

.loginform {
    width: 350px;
    padding: 35px 35px 15px 35px;
}
</style>



